export default {
	data() {
		return {
			login: false,
			user: '',
			password: ''
		};
	},
	onLoad() {
	},
	methods: {
		//跳转到注册页面
		toRegister: function() {
			uni.navigateTo({
				url: '../register/register'
			});
		},
		getUser() {
			console.log(this.user);
		},
		getPwd() {
			console.log(this.password);
		},
		onlogin() {
			if (this.user && this.password) {
				uniCloud.callFunction({
					name: 'login',
					data: {
						user_id: this.user,
						password: this.password
					},
					success(res) {
						console.log(res);
						console.log(res.result.status);
						if (res.result.status === 0) {
							uni.switchTab({
								url: '/pages/home/home',
								success() {
									uni.setStorage({
										key: 'token',
										data: 'ok'
									});
								}
							});
						} else {
							console.log('登陆失败');
						}
					}
				});
			} else {
				this.login = true;
			}
		}
	}
};
